@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&family=Roboto:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f0f0f0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  min-height: 100vh;
} 
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --primary-color: #4ecdc4;
  --primary-dark: #33a8a0;
  --secondary-color: #ff6b6b;
  --dark-bg: #1a1a2e;
  --darker-bg: #16213e;
  --light-text: #f5f5f5;
  --accent-color: #ffd166;
}

body {
  font-family: 'Montserrat', sans-serif;
  background-color: var(--dark-bg);
  color: var(--light-text);
  min-height: 100vh;
  overflow-x: hidden;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  padding: 2rem 1rem;
  background: radial-gradient(circle at center, #2a2a5a 0%, #1a1a2e 70%);
  position: relative;
  overflow: hidden;
}

/* Animated background elements */
.app::before, .app::after {
  content: '';
  position: absolute;
  width: 300px;
  height: 300px;
  border-radius: 50%;
  background: rgba(78, 205, 196, 0.05);
  filter: blur(60px);
  z-index: -1;
  animation: float 15s infinite alternate ease-in-out;
}

.app::before {
  top: -100px;
  left: -100px;
  background: rgba(78, 205, 196, 0.05);
}

.app::after {
  bottom: -100px;
  right: -100px;
  background: rgba(255, 107, 107, 0.05);
  animation-delay: -7s;
}

@keyframes float {
  0% {
    transform: translate(0, 0) scale(1);
  }
  50% {
    transform: translate(50px, 50px) scale(1.2);
  }
  100% {
    transform: translate(0, 0) scale(1);
  }
}

.app-header {
  margin-bottom: 2rem;
  text-align: center;
  position: relative;
}

h1 {
  font-size: 3.5rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  background: linear-gradient(135deg, var(--primary-color), var(--accent-color));
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  text-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  letter-spacing: 2px;
  animation: glow 3s infinite alternate;
}

@keyframes glow {
  0% {
    filter: drop-shadow(0 0 5px rgba(78, 205, 196, 0.3));
  }
  100% {
    filter: drop-shadow(0 0 15px rgba(78, 205, 196, 0.6));
  }
}

.author-credits {
  font-size: 1.1rem;
  font-weight: 300;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 1rem;
  letter-spacing: 1px;
}

.difficulty-selector {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 2rem;
  background: rgba(22, 33, 62, 0.7);
  padding: 1rem 2rem;
  border-radius: 12px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  animation: fadeIn 0.8s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.difficulty-label {
  font-size: 1.2rem;
  font-weight: 500;
  margin-bottom: 0.8rem;
  color: var(--light-text);
}

.difficulty-buttons {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
}

.difficulty-button {
  padding: 0.6rem 1.2rem;
  border: none;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.1);
  color: var(--light-text);
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.difficulty-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
  transition: all 0.5s ease;
}

.difficulty-button:hover::before {
  left: 100%;
}

.difficulty-button:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateY(-3px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.difficulty-button.active {
  background: linear-gradient(135deg, var(--primary-color), var(--primary-dark));
  color: white;
  transform: translateY(-3px);
  box-shadow: 0 6px 15px rgba(78, 205, 196, 0.3);
}

.game-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 2rem;
  gap: 2rem;
  margin-bottom: 2rem;
  width: 100%;
  max-width: 1200px;
  animation: slideIn 0.8s ease-out;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(-20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@media (min-width: 1200px) {
  .game-container {
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
  }
}

.app-footer {
  margin-top: 2rem;
  text-align: center;
  width: 100%;
  padding: 1.5rem;
  background: rgba(22, 33, 62, 0.7);
  border-radius: 12px;
  box-shadow: 0 -5px 20px rgba(0, 0, 0, 0.1);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  border-top: 1px solid rgba(255, 255, 255, 0.05);
}

.footer-content {
  max-width: 800px;
  margin: 0 auto;
}

.app-footer p {
  margin: 0.5rem 0;
  font-size: 0.95rem;
  color: rgba(255, 255, 255, 0.7);
  line-height: 1.6;
}

/* Responsive design */
@media (max-width: 1024px) {
  .game-container {
    flex-direction: column;
    align-items: center;
  }
  
  h1 {
    font-size: 2.8rem;
  }
}

@media (max-width: 768px) {
  .app {
    padding: 1rem 0.5rem;
  }
  
  h1 {
    font-size: 2.2rem;
  }
  
  .difficulty-selector {
    padding: 0.8rem 1.5rem;
  }
  
  .difficulty-button {
    padding: 0.5rem 1rem;
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  h1 {
    font-size: 1.8rem;
  }
  
  .author-credits {
    font-size: 0.9rem;
  }
  
  .difficulty-buttons {
    grid-gap: 0.5rem;
    gap: 0.5rem;
  }
  
  .difficulty-button {
    padding: 0.4rem 0.8rem;
    font-size: 0.8rem;
  }
  
  .app-footer p {
    font-size: 0.8rem;
  }
} 
.chess-board-container {
  position: relative;
  padding: 20px;
  background: linear-gradient(135deg, rgba(30, 41, 59, 0.9), rgba(15, 23, 42, 0.9));
  border-radius: 15px;
  box-shadow: 
    0 20px 40px rgba(0, 0, 0, 0.4),
    0 0 100px rgba(78, 205, 196, 0.1),
    inset 0 0 15px rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
  z-index: 10;
}

.chess-board-container:hover {
  box-shadow: 
    0 25px 50px rgba(0, 0, 0, 0.5),
    0 0 120px rgba(78, 205, 196, 0.15),
    inset 0 0 20px rgba(255, 255, 255, 0.07);
  transform: translateY(-5px);
}

.chess-board {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(8, 1fr);
  width: 560px;
  height: 560px;
  border: 2px solid rgba(255, 255, 255, 0.1);
  border-radius: 5px;
  overflow: hidden;
  box-shadow: inset 0 0 20px rgba(0, 0, 0, 0.3);
}

.square {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  height: 100%;
  transition: all 0.2s ease;
  cursor: pointer;
}

.square.light {
  background-color: #f0d9b5;
}

.square.dark {
  background-color: #b58863;
}

.square:hover {
  filter: brightness(1.1);
}

.square.highlighted {
  position: relative;
}

.square.highlighted::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 24px;
  height: 24px;
  background-color: rgba(78, 205, 196, 0.7);
  border-radius: 50%;
  z-index: 1;
  animation: pulse 1.5s infinite;
}

.square.selected {
  background-color: rgba(78, 205, 196, 0.5);
}

.square.check {
  background-color: rgba(255, 107, 107, 0.6);
  animation: checkPulse 1.5s infinite;
}

/* Add a special effect for the king in check */
.square.check .piece {
  filter: drop-shadow(0 0 8px rgba(255, 0, 0, 0.8));
  animation: kingInCheck 1s infinite alternate;
}

.square.last-move {
  background-color: rgba(255, 209, 102, 0.4);
}

.square.hint {
  position: relative;
}

.square.hint::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 30px;
  height: 30px;
  border: 3px dashed rgba(78, 205, 196, 0.8);
  border-radius: 50%;
  z-index: 1;
  animation: rotate 4s linear infinite;
}

@keyframes rotate {
  from {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  to {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}

.piece {
  width: 85%;
  height: 85%;
  position: relative;
  z-index: 2;
  filter: drop-shadow(0 5px 5px rgba(0, 0, 0, 0.4));
  transition: all 0.2s ease;
}

.piece:hover {
  transform: scale(1.1);
  filter: drop-shadow(0 8px 8px rgba(0, 0, 0, 0.5));
}

.piece.white {
  filter: drop-shadow(0 5px 5px rgba(0, 0, 0, 0.4)) brightness(1.05);
}

.piece.black {
  filter: drop-shadow(0 5px 5px rgba(0, 0, 0, 0.4)) brightness(0.95);
}

.captured-pieces {
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
  margin-top: 15px;
  padding: 10px;
  background: rgba(15, 23, 42, 0.5);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.05);
}

.captured-row {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 5px;
  gap: 5px;
  align-items: center;
  min-height: 30px;
}

.captured-piece {
  width: 30px;
  height: 30px;
  filter: drop-shadow(0 2px 2px rgba(0, 0, 0, 0.3));
  opacity: 0.8;
  transition: all 0.2s ease;
}

.captured-piece:hover {
  transform: scale(1.2);
  opacity: 1;
}

.board-coordinates {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  pointer-events: none;
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.7);
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
}

.files {
  position: absolute;
  bottom: 2px;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-around;
  padding: 0 35px;
}

.ranks {
  position: absolute;
  left: 5px;
  top: 0;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 20px 0;
}

@keyframes pulse {
  0% {
    transform: translate(-50%, -50%) scale(0.8);
    opacity: 0.7;
  }
  50% {
    transform: translate(-50%, -50%) scale(1);
    opacity: 0.9;
  }
  100% {
    transform: translate(-50%, -50%) scale(0.8);
    opacity: 0.7;
  }
}

@keyframes checkPulse {
  0% {
    box-shadow: 0 0 0 0 rgba(255, 107, 107, 0.7);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(255, 107, 107, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(255, 107, 107, 0);
  }
}

@keyframes kingInCheck {
  0% {
    transform: scale(1);
    filter: drop-shadow(0 0 5px rgba(255, 0, 0, 0.8));
  }
  100% {
    transform: scale(1.1);
    filter: drop-shadow(0 0 10px rgba(255, 0, 0, 0.9));
  }
}

/* Media queries for responsive design */
@media (max-width: 768px) {
  .chess-board {
    width: 480px;
    height: 480px;
  }
  
  .piece {
    width: 80%;
    height: 80%;
  }
}

@media (max-width: 576px) {
  .chess-board {
    width: 320px;
    height: 320px;
  }
  
  .piece {
    width: 75%;
    height: 75%;
  }
  
  .square.highlighted::before {
    width: 16px;
    height: 16px;
  }
  
  .captured-piece {
    width: 20px;
    height: 20px;
  }
  
  .files, .ranks {
    font-size: 0.8rem;
  }
}

@media (max-width: 360px) {
  .chess-board {
    width: 280px;
    height: 280px;
  }
} 
