.ai-thinking {
  display: flex;
  align-items: center;
  margin-top: 10px;
  background-color: rgba(255, 255, 255, 0.1);
  padding: 8px 12px;
  border-radius: 8px;
  animation: pulse 1.5s infinite;
  box-shadow: 0 0 10px rgba(0, 0, 255, 0.3);
}

.ai-thinking span {
  font-weight: 500;
  color: #4dabf7;
  margin-right: 8px;
}

.thinking-dots {
  display: flex;
  align-items: center;
}

.dot {
  width: 6px;
  height: 6px;
  background-color: #4dabf7;
  border-radius: 50%;
  margin: 0 3px;
  opacity: 0.6;
}

.dot:nth-child(1) {
  animation: dot-animation 1.5s infinite;
}

.dot:nth-child(2) {
  animation: dot-animation 1.5s infinite 0.5s;
}

.dot:nth-child(3) {
  animation: dot-animation 1.5s infinite 1s;
}

@keyframes dot-animation {
  0% {
    transform: scale(1);
    opacity: 0.6;
  }
  50% {
    transform: scale(1.5);
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 0.6;
  }
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 10px rgba(0, 0, 255, 0.3);
  }
  50% {
    box-shadow: 0 0 15px rgba(0, 0, 255, 0.5);
  }
  100% {
    box-shadow: 0 0 10px rgba(0, 0, 255, 0.3);
  }
} 
.promotion-dialog-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  animation: fadeIn 0.3s ease-in-out;
}

.promotion-dialog {
  position: absolute;
  background: rgba(15, 23, 42, 0.95);
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.1);
  min-width: 300px;
  transform: translate(-50%, -50%);
  animation: scaleIn 0.3s ease-in-out;
  z-index: 1001;
}

.promotion-title {
  color: #f5f5f5;
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 15px;
  text-align: center;
  letter-spacing: 1px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  padding-bottom: 10px;
}

.promotion-options {
  display: flex;
  justify-content: space-around;
  grid-gap: 10px;
  gap: 10px;
}

.promotion-piece {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s ease;
  background: rgba(255, 255, 255, 0.05);
}

.promotion-piece:hover {
  background: rgba(78, 205, 196, 0.2);
  transform: translateY(-5px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.promotion-piece-image {
  width: 50px;
  height: 50px;
  margin-bottom: 5px;
  filter: drop-shadow(0 2px 3px rgba(0, 0, 0, 0.4));
}

.promotion-piece-name {
  color: #f5f5f5;
  font-size: 0.9rem;
  text-transform: capitalize;
  font-weight: 500;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes scaleIn {
  from { transform: translate(-50%, -50%) scale(0.8); opacity: 0; }
  to { transform: translate(-50%, -50%) scale(1); opacity: 1; }
}

@media (max-width: 480px) {
